  position: relative;
  width: 100px;
  height: 35px;
  background: #bdc3c7;
  -webkit-appearance: none;
  border-radius: 20px;
  outline: none;
  transition: 0.4s;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  padding-top: 0;
}
.toggle input:checked[type=checkbox] {
  background: #c11627;
}
.toggle input[type=checkbox]::before {
  z-index: 2;
  position: absolute;
  content: "";
  left: 0;
  width: 40px;
  height: 35px;
  background: #e1e1e1;
  border-radius: 50%;
  transform: scale(1.1);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 0.4s;
}
.toggle input:checked[type=checkbox]::before {
  left: 60px;
}
